export default {
  props: {
    imgs: {
      default: [],
      type: Array
    },
    currentImg: {
      default: '',
      type: String
    },
    show: {
      default: false,
      type: Boolean
    }
  },
  data () {
    return {
      current: '',
      currentX: '',
      currentIndex: ''
    }
  },
  watch: {
    currentImg (val,old) {
      this.current = this.currentImg
      this.getIndex(val)
    }
  },
  mounted () {
  },
  methods: {
    startChange (e) {
      this.currentX = e.changedTouches[0].clientX
    },
    changeImg (e) {
      let distance = e.changedTouches[0].clientX - this.currentX
      if (-10< distance && distance <10) {
        this.$emit('close')
      } else if (distance < -50) {
        this.swipeLeft()
      } else {
        this.swipeRight()
      }
    },
    getIndex (url) {
      for (let i = 0,len = this.imgs.length;i < len;i ++) {
        if (this.imgs[i] == url) {
          this.currentIndex = i
          break
        }
      }
    },
    swipeLeft () {
      if (this.currentIndex != (this.imgs.length - 1)) {
        this.currentIndex++
      } else {
        this.currentIndex = 0
      }
      this.current = this.imgs[this.currentIndex]
    },
    swipeRight () {
      if (this.currentIndex == 0) {
        this.currentIndex = this.imgs.length - 1
      } else {
        this.currentIndex--
      }
      this.current = this.imgs[this.currentIndex]
    },
  }
}



