

import Vue from 'vue'
import store from './store'

import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import routes from './router'
import config from './config'
import App from './App'
import api from './api'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

Vue.use(DatetimePlugin)
Vue.use(ToastPlugin)
Vue.use(VueRouter)
Vue.use(LoadingPlugin)



Vue.prototype.$api = api
Vue.prototype.config = config
Vue.prototype.Vue = Vue



Vue.component('loading', Loading)
Vue.component('toast', Toast)

const router = new VueRouter({
routes
})
router.beforeEach((to, from, next) => {
document.body.scrollTop = 0

if (window.counts && window.counts.length > 0) {
window.counts.forEach((e, index) => {
clearInterval(window.counts[index])
})
}

if (window.orderCount) {
clearInterval(window.orderCount)
}
next()











})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store: store,
render: h => h(App)
}).$mount('#app-box')



