export default {
	name: 'switchComponent',
	props: {
      value: {
        type: Boolean,
        default: true
      },
      text: {
      	type: String,
      	default: ''
      }
    },
	data () {
		return {
			isChecked: this.value
		}
	},
	computed: {
		direction () {
			if (this.text) {
				return this.text.split('|')
			} else {
				return []
			}
		}
	},
	watch: {
      isChecked(val) {
        this.$emit('change', val);
      }
    },
    methods: {
      toggle() {
        this.isChecked = !this.isChecked;
      }
    }
}	



